<template>
    <div >
        <top-nav title=""></top-nav>
        <div  class="wyu-main">
            <div class="wyu-block">
                <div class="revenue-time">
                    <el-form :inline="true" size="mini" :model="searchForm" style="background: white;padding-bottom:0px;" class="search-form">
                        <el-form-item>
                        <el-date-picker v-model="searchForm.date" type="daterange" align="right" size="mini"
                                unlink-panels range-separator="" start-placeholder="" end-placeholder=""
                                :picker-options="pickerOptions2" value-format="yyyy-MM-dd">
                        </el-date-picker>
                        </el-form-item>
                        <el-form-item style="margin-top: -1px">
                            <el-button size="mini" type="primary" @click="getDatas(),getHotGoods(),loading=true"></el-button>
                        </el-form-item>
                    </el-form>

                </div>

                <el-row>
                    <el-col :span="12">
                        <span class="sj-tit">:</span>
                        <div class="sj-info">
                            <p></p>
                            <p> 1,</p>
                            <p> 207</p>
                            <p> 3</p>
                        </div>
                    </el-col>
                    <el-col :span="12">
                        <el-radio-group class="sj-radio" @change="getDatas(),getHotGoods()" v-model="searchForm.type" size="mini">
                            <el-radio-button label="" ></el-radio-button>
                            <el-radio-button label="weixin"></el-radio-button>
                            <el-radio-button label="ali"></el-radio-button>
                            <el-radio-button label="baidu"></el-radio-button>
                            <el-radio-button label="toutiao"></el-radio-button>
                            <el-radio-button label="h5">H5</el-radio-button>
                        </el-radio-group>
                    </el-col>
                </el-row>
                
            </div>
        </div>

        <div  class="wyu-main" v-loading="loading">
            <div class="wyu-block" style="padding-bottom:20px;">
                <div class="wyu-title" style="margin-bottom:0px;">
                    <h2></h2>
                </div>

                <el-row :gutter="40">
                    <el-col :span="6">
                        <div class="sj-item">
                            <div class="name"></div>
                            <div class="num"><i></i>{{datas.order_money}}</div>
                        </div>
                    </el-col>
                    <el-col :span="6">
                        <div class="sj-item">
                            <div class="name"></div>
                            <div class="num"><i></i>{{datas.order_num}}</div>
                        </div>
                    </el-col>
                    <el-col :span="6">
                        <div class="sj-item">
                            <div class="name"></div>
                            <div class="num"><i></i>{{datas.integral_order_money}}</div>
                        </div>
                    </el-col>
                    <el-col :span="6">
                        <div class="sj-item">
                            <div class="name"></div>
                            <div class="num"><i></i>{{datas.integral_order_num}}</div>
                        </div>
                    </el-col>
                    <el-col :span="6">
                        <div class="sj-item">
                            <div class="name">VIP</div>
                            <div class="num"><i></i>{{datas.vip_order_money}}</div>
                        </div>
                    </el-col>
                    <el-col :span="6">
                        <div class="sj-item">
                            <div class="name">VIP</div>
                            <div class="num"><i></i>{{datas.vip_order_num}}</div>
                        </div>
                    </el-col>
                    <el-col :span="6">
                        <div class="sj-item">
                            <div class="name"></div>
                            <div class="num"><i></i>{{datas.recharge_order_money}}</div>
                        </div>
                    </el-col>
                    <el-col :span="6">
                        <div class="sj-item">
                            <div class="name"></div>
                            <div class="num"><i></i>{{datas.order_fahuo_num}}</div>
                        </div>
                    </el-col>
                    <el-col :span="6">
                        <div class="sj-item">
                            <div class="name"></div>
                            <div class="num"><i></i>{{datas.integral_order_fahuo_num}}</div>

                        </div>
                    </el-col>
                    <el-col :span="6">
                        <div class="sj-item">
                            <div class="name"></div>
                            <div class="num"><i></i>{{datas.tuikuan_order_num}}</div>

                        </div>
                    </el-col>
                </el-row>
            </div>
        </div>


        <div  class="wyu-main">
            <div class="wyu-block" style="padding-bottom:20px;">
                <div class="wyu-title" style="margin-bottom:0px;">
                    <h2>()</h2>
                </div>
                <el-row>
                    <v-chart style="width: 100%" :options="option"/>
                </el-row>
            </div>
        </div>

        <div  class="wyu-main">
            <el-row :gutter="40">
                <el-col :span="12">
                     <div class="wyu-block" style="position: relative;height: 486px;">
                         <div class="wyu-title">
                            <h2>Top5</h2>
                         </div>
                          <!--<div class="btn-list">-->
                            <!--<el-button size="small" type="primary" plain></el-button>-->
                            <!--<el-button size="small" plain></el-button>-->
                          <!--</div>-->
                          <div class="">
                              <el-table :data="hotGoods" style="width: 100%;" >
                                  <el-table-column label="" width="100">
                                      <template slot-scope="scope">
                                          <img :src="scope.row.goods_photo" width="30px"/>
                                      </template>
                                  </el-table-column>
                                  <el-table-column label="" width="600">
                                      <template slot-scope="scope">
                                          <p class="ellipsis-01">{{scope.row.goods_name}}</p>
                                      </template>
                                  </el-table-column>

                                  <el-table-column label="" >
                                      <template slot-scope="scope">
                                        <p >{{scope.row.order_num}}</p>
                                      </template>
                                  </el-table-column>
                              </el-table>
                          </div>
                     </div>
                </el-col>
                <el-col :span="12">
                    <div class="wyu-block" >
                        <div class="wyu-title">
                           <h2></h2>
                        </div>

                        <el-row :span="12">
                           <el-col :span="4"></el-col>
                           <el-col :span="20"  style="margin: 0 100px;">
                                   <v-chart :options="hotDevice"/>
                           </el-col>
                        </el-row>
                    </div>
                </el-col>
            </el-row>
        </div>   


    </div>
</template>

<script>
    import TopNav from "@/components/TopNav";

    import ECharts from 'vue-echarts'
    import 'echarts/lib/chart/line'
    import 'echarts/lib/chart/pie'
    import 'echarts/lib/chart/bar'
    import 'echarts/lib/component/tooltip'
    import 'echarts/lib/component/legend'
    import 'echarts/lib/component/title.js'
    import 'echarts/lib/component/toolbox.js'
    import 'echarts/lib/component/legend.js'
    import 'echarts/lib/component/markLine.js'

    import * as api from "@/api";
    export default {
        name: 'index',
        components: {TopNav, 'v-chart': ECharts},
        data(){
            return {
                loading: false,
                type:0,
                searchForm: {
                    date: [],
                    type:0,
                },
                pickerOptions2: {
                    shortcuts: [{
                        text: '',
                        onClick(picker) {
                            const end = new Date();
                            const start = new Date();
                            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                            picker.$emit('pick', [start, end]);
                        }
                    }]
                },
                datas: {
                    'order_money': '',
                    'order_num': '',
                    'integral_order_money': '',
                    'integral_order_num': '',
                    'vip_order_money': '',
                    'vip_order_num': '',
                    'recharge_order_money': '',
                    'order_fahuo_num': '',
                    'integral_order_fahuo_num': '',
                    'tuikuan_order_num': '',
                },
                hotGoods: [],
                //
                hotDevice: {
                    title : {
                        text: '',
                        subtext: '',
                        x:'center'
                    },
                    tooltip : {
                        trigger: 'item',
                        formatter: "{a} <br/>{b} : {c} ({d}%)"
                    },
                    legend: {
                        orient : 'vertical',
                        x : 'right',
                        data:['h5','','','','']
                    },
                    calculable : true,
                    series : [
                        {
                            name:'',
                            type:'pie',
                            radius : '55%',
                            center: ['50%', '60%'],
                            data:[
                                {value:0, name:'H5'},
                                {value:0, name:''},
                                {value:0, name:''},
                                {value:0, name:''},
                                {value:0, name:''}

                            ]
                        }
                    ]
                },
                //
                option: {
                    title : {
                        text: '',
                    },
                    tooltip : {
                        trigger: 'axis'
                    },
                    legend: {
                        data:['','','VIP','']
                    },

                    calculable : true,
                    xAxis : [
                        {
                            type : 'category',
                            boundaryGap : false,
                            data : []
                        }
                    ],
                    yAxis : [
                        {
                            type : 'value',
                            axisLabel : {
                                formatter: '{value}'
                            }
                        }
                    ],
                    series : [
                        {
                            name:'',
                            type:'line',
                            data:[],
                        },
                        {
                            name:'',
                            type:'line',
                            data:[],
                        },
                        {
                            name:'VIP',
                            type:'line',
                            data:[],
                        },
                        {
                            name:'',
                            type:'line',
                            data:[],
                        }
                    ]
                },
            }
        },

        created () {
            this.getDatas();
            this.getHotGoods();
            this.getHotDevice();
            this.getRevenue();
        },

        methods:{
            getDatas(){
                this.loading = true;
                api.post("/shop/admin.finance.shop/index", {
                    appToken: this.$store.getters.appToken,
                    date_begin: this.searchForm.date[0],
                    date_end: this.searchForm.date[1],
                    device:this.searchForm.type,
                }).then(res => {
                    this.datas = res;
                    this.loading = false;
                }).catch((error) => {
                    this.loading = false;
                });
            },



            getHotGoods() {
                this.loading = true;
                api.post("/shop/admin.finance.shop/hotGoods", {
                    appToken: this.$store.getters.appToken,
                    date_begin: this.searchForm.date[0],
                    date_end: this.searchForm.date[1],
                    device:this.searchForm.type,
                }).then(res => {
                    this.hotGoods = res;
                    this.loading = false;
                }).catch((error) => {
                    this.loading = false;
                });
            },



            getHotDevice() {
                this.loading = true;
                api.post("/shop/admin.finance.shop/hotDevice", {
                    appToken: this.$store.getters.appToken,
                    date_begin: this.searchForm.date[0],
                    date_end: this.searchForm.date[1],

                }).then(res => {
                    let deviceData = [];
                    for (let i in res) {
                        let data = {};
                        data.value = res[i];
                        data.name = i;
                        deviceData.push(data);
                    }
                    this.hotDevice.series[0].data = deviceData;
                    this.loading = false;
                }).catch((error) => {
                    this.loading = false;
                });
            },


            getRevenue() {
                this.loading = true;
                api.post("/shop/admin.finance.shop/revenue", {
                    appToken: this.$store.getters.appToken,
                    date_begin: this.searchForm.date[0],
                    date_end: this.searchForm.date[1],

                }).then(res => {
                    this.option.xAxis[0].data = res.date;
                    let order_data = [];
                    for (let i in res.order_shouru)  {
                        order_data.push(res.order_shouru[i]);
                    }
                    this.option.series[0].data = order_data;

                    let integral_order_data = [];
                    for (let i in res.integral_order_shouru)  {
                        integral_order_data.push(res.integral_order_shouru[i]);
                    }
                    this.option.series[1].data = integral_order_data;

                    let vip_order_data = [];
                    for (let i in res.vip_order_shouru)  {
                        vip_order_data.push(res.vip_order_shouru[i]);
                    }
                    this.option.series[2].data = vip_order_data;

                    let recharge_order_data = [];
                    for (let i in res.recharge_order_shouru)  {
                        recharge_order_data.push(res.recharge_order_shouru[i]);
                    }
                    this.option.series[3].data = recharge_order_data;

                    this.loading = false;
                }).catch((error) => {
                    this.loading = false;
                });
            },

           
        },
    }
</script>

<style scoped>
    .btn-list{
        position:absolute;
        right: 10px;
        top:10px;
    }
    .sj-radio{
        float: right;
    }

    .sj-tit{
        float: left;
        width: 80px;
         font-size: 13px;
        color:#999999;
        margin-left: 10px;
         line-height: 20px;
    }
    .sj-info{
        float: left;
         font-size: 13px;
        color:#999999;
        line-height: 20px;
    }

    .sj-item{
        margin-top: 20px;
        background: #F8F9FB;
        height:120px;
        text-align: center;
    }
    .sj-item .name{
        color:#333333;
        font-size: 18px;
        padding-top: 20px;
    }
    .sj-item .num{
        font-weight: 400;
        font-size: 36px;
        color:#005DE5;
        margin-top: 10px;
    }
    .sj-item .num i{
        font-size: 20px;
    }
     .sj-item .duibi{
         margin-top: 20px;
         font-size: 13px;
         color: #333333;
     }
     .sj-item .duibi .zhang{
         color: #FD6329;
     }
     .sj-item .duibi .die{
         color: #00BF63;
     }
</style>
